.top-nav {
  height: var(--top-nav-height);
  width: 100%;
  background: url("https://blog.wexer.com/hubfs/Wexer_Jan2018/2017/04/Included-in-all-packages_1300x695_acf_cropped_1300x500_acf_cropped_1300x500_acf_cropped.jpg?t=1523020065619")
    no-repeat center;
  background-size: cover;
  border-bottom: 1px var(--blue) solid;
}

.top-nav-wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.top-nav-wrapper > *:not(img):not(:first-child) {
  padding: 10px;
  margin: 0 10px;
  color: black;
  background: white;
  font-size: 1.3rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.top-nav-wrapper > a:not(:first-child):hover {
  background: var(--d-blue);
  color: white !important;
}

.top-nav-wrapper > *:hover > span {
  background: var(--d-blue);
  color: white !important;
}

.top-nav-wrapper > *:first-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-right: auto;
}

.logo {
  padding: 5px;
  width: 220px;
  margin-left: 20px;
}

.cart-link-active {
  -webkit-transform: translateX(-20vw) translateY(170px) scale(4);
      -ms-transform: translateX(-20vw) translateY(170px) scale(4);
          transform: translateX(-20vw) translateY(170px) scale(4);
  -webkit-transition: -webkit-transform 0.6s ease-in-out;
  transition: -webkit-transform 0.6s ease-in-out;
  -o-transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  background: transparent !important;
  color: white !important;
}

.cart-link-active span {
  color: white !important;
}

.mobile-menu-btn {
  display: none !important;
  z-index: 3;
}

.mobile-menu {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2;
  background: white;
  height: 100%;
  width: 40%;
  margin: 0px !important;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  display: none;
}

.mobile-menu span {
  color: black !important;
}

.mobile-menu > * {
  padding: 20px 0;
}

@media (max-width: 800px) {
  .cart-link-active {
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
  }
  .mobile-menu {
    display: -ms-flexbox;
    display: flex;
  }
  .mobile-menu-btn {
    display: block !important;
  }
  .top-nav-wrapper > a {
    display: none;
  }
}



