.index-overview {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}

.index-overview > * {
  margin: 10px;
}

.btn {
  padding: 10px 15px;
  background: var(--blue);
  color: white;
  border: var(--blue) solid 1px;
  -webkit-box-shadow: 1px 1px 1px rgba(63, 60, 60, 0.267);
          box-shadow: 1px 1px 1px rgba(63, 60, 60, 0.267);
  font-size: 1.3rem;
  font-family: "Karla", sans-serif;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn:hover {
  background: var(--d-blue);
  border: var(--d-blue) solid 1px;
  outline: none;
}

.btn:focus {
  outline: none;
}

.animate {
  -webkit-animation: fadeIn 0.3s ease-in;
          animation: fadeIn 0.3s ease-in;
}



