@-webkit-keyframes fade {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-45px);
            transform: translateY(-45px);
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-45px);
            transform: translateY(-45px);
  }
  100% {
    opacity: 1;
  }
}

.cart-window {
  position: absolute;
  -webkit-animation: fade 0.2s ease-in;
          animation: fade 0.2s ease-in;
  -webkit-box-shadow: rgb(73, 68, 68) 0.3px 0.3px 1px;
          box-shadow: rgb(73, 68, 68) 0.3px 0.3px 1px;
  z-index: 3;
}

.cart-body {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  color: var(--d-blue);
  background-color: white;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  min-width: 300px;
}

.cart-body > * {
  margin: 5px;
}



